
// @ is an alias to /src test env
import { reactive, ref } from "vue";
import * as moment from "moment";
import Swal from "sweetalert2";
import Auth from "../../services/routes";
export default {
  name: "Home",
  components: {
  },
  data() {
    return {
      render: false,
      fileImage: null,
      showModal: false,
      fileName: false,
      activetab: 1,
      nome_musculo: null,
      imagem_musculo: null,
      load: false,
      tableData: [

        {
          nome: 'Yoga',
        },
        {
          nome: 'Yoga',
        },
        {
          nome: 'Yoga',
        },
        {
          nome: 'Yoga',
        },
      ],
    };
  },
  methods: {
    showFile() {
      var file = this.$refs.loadImage.files[0];
      const getBase64 = (file) =>
        new Promise(function (resolve, reject) {
          let reader = new FileReader();
          reader.readAsDataURL(file);
          reader.onload = () => resolve(reader.result);
          reader.onerror = (error) => reject("Error: ", error);
        });
      getBase64(file)
        .then((result) => {
          this.render = result;
        })
        .catch((e) => console.log("deu erro:", e));
    },

    SendImage() {
      this.load = true;
      let data = {
        nome_musculo: this.nome_musculo,
      }
      Auth.CadastroCategoria(data)
        .then((r) => {
          this.$notify({
            message: "Cadastrado com Sucesso!",
            title: "Sucesso",
            type: "success",
          });
        })
        .catch((e) => {
          this.$notify({
            message: "Não foi possivel fazer esta ação",
            title: "Erro",
            type: "danger",
          });
        })
        .finally(() => {
          this.showModal = false;
          this.load = false;
          this.render = false;
          this.nome_musculo = null;
        });
    },
  },
  mounted() {

  },
  created() { },
  setup() {
    const state = reactive({
      testes: "dCasa",
      numero: ref(0),
    });

    return {
      state,
    };
  },
};
